import React from "react";
import Image from 'next/image';
import 'bootstrap/dist/css/bootstrap.min.css';
import Card from 'react-bootstrap/Card';

interface JobCardProps {
    title: string;
    company: string;
    image: string;
    alt: string;
    year: string;
    description: string;
}

export default function JobCard({ title, company, image, alt, year, description }: JobCardProps) {
    return (
        <Card className="bg-transparent text-white pl-[5%] pr-[5%]">
            <div className="flex items-center gap-4 p-4">
                <div className="border-4 border-pink-500">
                    <Image 
                    src={image} 
                    alt={alt} 
                    width={400}
                    height={400}
                    className="object-contain rounded-md"/>
                </div>
                <Card.Body className="p-0">
                    <Card.Title className="!mb-1">
                        {title} | {company}
                    </Card.Title>
                    <Card.Subtitle>{year}</Card.Subtitle>
                    <Card.Text>{description}</Card.Text>
            </Card.Body>
            </div>
        </Card>
    );
}